.User {
    display: flex;
    align-items: flex-start;
    padding: 1rem;
    &:hover{
      background: rgba(0, 0, 0, 0.2);
      cursor: pointer;
    };
 }
 .User__details {
    display: none;
 }
 .User__pic {
    width: 50px;
    border-radius: 50%;
 }
 @media (min-width: 576px) {
   .User__details {
     display: block;
     padding: 0 0 0 1rem;
   }
   .User__details-name {
     margin: 0;
     color: rgba(255, 255, 255, 0.8);
     font-size: 1rem;
  } 
}